@import 'forms/search-form';

form {
  margin-bottom: rr-gridbase(4);

  .icon-only {
    .icon-label {
      display: block;

      .ic {
        position: relative;
        top: .1em;
        margin-right: 0;
      }

      .label {
        position: absolute;
        left: -999999em;
      }
    }
  }

  .control-group {
    display: flex;

    input,
    textarea {
      flex: 1 1 auto;
      width: 100%;
    }

    button {
      &.icon-only {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 .3em;
      }
    }
  }

  label,
  select {
    @include rr-font-compute(l, tight);
    font-weight: 500;
    display: block;

    &.form-required {
      &::after {
        color: color(alert);
        content: '*';
      }
    }
  }

  .description {
    margin-top: rr-gridbase(1);
    @include rr-font-compute(s, medium);
  }

  input[type='text'],
  input[type='password'],
  input[type='color'],
  input[type='date'],
  input[type='datetime'],
  input[type='email'],
  input[type='month'],
  input[type='number'],
  input[type='range'],
  input[type='search'],
  input[type='tel'],
  input[type='time'],
  input[type='ur'],
  input[type='week'],
  textarea {
    @include rr-font-compute(r, medium);
    background-color: color(wildcat-white);
    padding: rr-grid-remainder(1px) rr-gridbase(1);
    border-width: 1px;
    border-style: solid;
    border-radius: 0;
  }
  
  input, textarea, select {
    max-width: 100%;
  }

  .form-item {
    margin-bottom: rr-gridbase(2);
  }
}
